import React, { createContext, useEffect, useState } from 'react';
import '../css/2049.scss';
import { set } from '@project-serum/anchor/dist/cjs/utils/features';
import { useCallback } from 'react';
import { useContext } from 'react';
import { addEventListener, removeEventListener } from 'react';

const LEFT = 0;
const UP = 1;
const RIGHT = 2;
const DOWN = 3;

export const Game2049Context = createContext();

const useGame2049 = () => {
    const context = useContext(Game2049Context);
    if (!context) {
        throw new Error("Missing Lottery context");
    }
    return context;
}

export const Game2049ContextProvider = (props) => {
    
    class Tile {
        constructor(x, y, value, merged) {
            this.x = x;
            this.y = y;
            this.value = value;
            this.merged = merged;
        }
    }
    const [ gameBoard, updateGameBoard] = useState([new Tile(0, 0, 2, false), new Tile(0, 1, 2, false), new Tile(0, 2, 2, false)]);
    const [ score, setScore ] = useState(0);
    const [ gameOver, setGameOver ] = useState(false);
    const [ lastDirection, setLastDirection ] = useState();

    const handleUserKeyPress = useCallback(event => {
        const { key, keyCode } = event;
        let dir = 0;
        let process = false;
        let board = gameBoard;
        //console.log(board)
        if (keyCode === 37 ){
            setLastDirection(LEFT)
            dir = LEFT;
            process = true;
        } else if (keyCode === 38) {
            setLastDirection(UP)
            dir = UP;
            process = true;
        }
        else if (keyCode === 39) {
            setLastDirection(RIGHT)
            dir = RIGHT;
            process = true;
        }
        else if (keyCode === 40) {
            setLastDirection(DOWN)
            dir = DOWN;
            process = true;
        }
        console.log("processed: ", process)
        if (process) {
            let board = move(dir);
        
            updateGameBoard(board);
            if (gameBoard.length <= 8) {
                for (let i = 0; i < 2; i++) {
                    let new_tile = generateNewTile(dir);
                    board.push(new_tile);
                    updateGameBoard(board)
                }
            } else {
                    let new_tile = generateNewTile(dir);
                    console.log("new tile: ", new_tile);
                    board.push(new_tile);
                    updateGameBoard(board)
                }
            if (check_over()) {
                setGameOver(true);
                console.log("Game over");
            }
        }
        
        
        
    }, [lastDirection, gameBoard]);

    useEffect(() => {
        window.addEventListener('keyup', handleUserKeyPress);

        return () => {
            window.removeEventListener('keyup', handleUserKeyPress);
        };
    }, [handleUserKeyPress]);
    

    // Board level
    function move(direction) {
        if (direction === undefined) {
            return gameBoard;
        }
        let board = gameBoard.sort((a, b) => {
            if (direction === UP) {
                if (a.x - b.x < 0) {
                    return 1;
                } else if (a.x - b.x > 0) {
                    return -1;
                } else {
                    if (a.y - b.y < 0) {
                        return -1;
                    } else if (a.y - b.y > 0) {
                        return 1;
                    }
                }
            } else if (direction === DOWN) {
                if (a.x - b.x < 0) {
                    return 1;
                } else if (a.x - b.x > 0) {
                    return -1;
                } else {
                    if (a.y - b.y < 0) {
                        return 1;
                    } else if (a.y - b.y > 0) {
                        return -1;
                    }
                }
            } else if (direction === LEFT) {
                if (a.y - b.y < 0) {
                    return 1;
                } else if (a.y - b.y > 0) {
                    return -1;
                } else {
                    if (a.x - b.x < 0) {
                        return -1;
                    } else if (a.x - b.x > 0) {
                        return 1;
                    }
                }
            } else {
                if (a.y - b.y < 0) {
                    return 1;
                } else if (a.y - b.y > 0) {
                    return -1;
                } else {
                    if (a.x - b.x < 0) {
                        return 1;
                    } else if (a.x - b.x > 0) {
                        return -1;
                    }
                }
            };
            return 0;
        });
        console.log("Direction: ", direction);
        //console.log("Board: ", board)


        let new_board = [];
        for (let i = 0; i < board.length; i++) {
            if (board[i].merged === true) {
                setScore(score + board[i].value);
                continue;
            }
            
            if (direction === UP) {
                if (board[i].x !== board[i - 1]?.x) {
                    board[i].y = 0;
                } else {
                    board[i].y = new_board[new_board.length - 1].y + 1;
                }
                if (board[i].value === board[i + 1]?.value && board[i].x === board[i + 1]?.x) {
                    board[i+1].merged = true;
                    board[i].value = board[i].value + board[i + 1].value;
                }
                
            } else if (direction === DOWN) {
                if (board[i].x !== board[i - 1]?.x) {
                    board[i].y = 3;
                } else {
                    board[i].y = new_board[new_board.length - 1].y - 1;
                }
                if (board[i].value === board[i + 1]?.value && board[i].x === board[i + 1]?.x) {
                    board[i+1].merged = true;
                    board[i].value = board[i].value + board[i + 1].value;
                }
            } else if (direction === LEFT) {
                    console.log(i, board[i].y, board[i - 1]?.y, board[i].y !== board[i - 1]?.y)
                    //console.log("board", board)
                if (board[i].y !== board[i - 1]?.y) {
                    board[i].x = 0;
                } else {
                    // console.log("new_board[new_board.length - 1].x: ", new_board.length)
                    board[i].x = new_board[new_board.length - 1].x + 1;
                }
                if (board[i].value === board[i + 1]?.value && board[i].y === board[i + 1]?.y) {
                    board[i+1].merged = true;
                    board[i].value = board[i].value + board[i + 1].value;
                }
            } else if (direction === RIGHT) {
                if (board[i].y !== board[i - 1]?.y) {
                    board[i].x = 3;
                } else {
                    board[i].x = new_board[new_board.length - 1].x - 1;
                    //console.log("board[i].value === board[i+1]?.value: ", board[i].value === board[i + 1]?.value);
                }
                if (board[i].value === board[i + 1]?.value && board[i].y === board[i + 1]?.y) {
                    board[i+1].merged = true;
                    board[i].value = board[i].value + board[i + 1].value;
                }
            }
            new_board.push(board[i]);
        }

        //console.log("new board: ", new_board);
        return new_board;
    }
      
    function generateNewTile(lastmove) {
        let new_value = Math.random() < 0.9 ? 2 : 4;
        let new_tile = new Tile(0, 0, new_value, false);
        let rand = Math.floor(Math.random() * 4);
        let count = 0;

        if (lastmove === UP) {
            new_tile.x = rand;
            new_tile.y = 3;
                console.log("rand: ", rand);
            while (getTile(rand, 3)) {
                count+=1;
                console.log("rand: ", rand);
                rand = Math.floor(Math.random() * 4);
                new_tile.x = rand;
                if (count > 20) {
                    setGameOver(true);
                    console.log("Game over");
                    break;
                }
            }
        } else if (lastmove === DOWN) {
            new_tile.x = rand;
            new_tile.y = 0;
            while (getTile(rand, 0)) {
                count += 1
                console.log("rand: ", rand);
                rand = Math.floor(Math.random() * 4);
                new_tile.x = rand;
                if (count > 20) {
                    setGameOver(true);
                    console.log("Game over");
                    break;
                }
            }
        } else if (lastmove === LEFT) {
            new_tile.x = 3;
            new_tile.y = rand;
            while (getTile(3, rand)) {
                count += 1
                console.log("rand: ", rand);
                rand = Math.floor(Math.random() * 4);
                new_tile.y = rand;
                if (count > 20) {
                    setGameOver(true);
                    console.log("Game over");
                    break;
                }
            }
        } else if (lastmove === RIGHT) {
            new_tile.x = 0;
            new_tile.y = rand;  
            while (getTile(0, rand)) {
                count += 1
                console.log("rand: ", rand);
                rand = Math.floor(Math.random() * 4);
                new_tile.y = rand;
                if (count > 20) {
                    setGameOver(true);
                    console.log("Game over");
                    break;
                }
            }
        } else {
            new_tile.x = rand;
            new_tile.y = 3;
                console.log("rand: ", rand);
            while (getTile(rand, 3)) {
                count += 1
                console.log("rand: ", rand);
                rand = Math.floor(Math.random() * 4);
                new_tile.x = rand;
                if (count > 20) {
                    setGameOver(true);
                    console.log("Game over");
                    break;
                }
            }
        }
        
        return new_tile;
    }

    function getTile(x, y) {
        for (let i = 0; i < gameBoard.length; i++) {
            if (gameBoard[i].x === x && gameBoard[i].y === y) {
                return gameBoard[i];
            }
        }
        console.log("Tile not found");
        return null;
    }

    function check_over() {
        for (let i = 0; i < 4; i++) {
            if (move(i).length < 16) {
                return false;
            }
        }
        return true;
    }
    return (
        <Game2049Context.Provider value={{
            gameBoard, 
            score, 
            gameOver 
        }}>
            {props.children}
        </Game2049Context.Provider>
    )
};